@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  font-family: 'Lato', sans-serif;
}

body {
  box-sizing: border-box;
  color: var(--color-black);
}

:root{--color-black: #333;--color-black-light: #555;--color-clouds: #ecf0f1;--color-blue: #3498db;--color-blue-dark: #2980b9;--color-green: #2ecc71;--color-green-dark: #27ae60;--color-yellow: #f1c40f;--color-yellow-dark: #ddb30c;--color-orange: #f39c12;--color-orange-dark: #e69412;--color-red: #e74c3c;--color-red-dark: #c0392b;--color-silver: #bdc3c7;--color-asbestos: #7f8c8d}.app{height:100vh;display:flex;justify-content:center;background:#e0eafc;background:-webkit-gradient(linear, left top, right top, from(#cfdef3), to(#e0eafc));background:linear-gradient(to right, #cfdef3, #e0eafc)}.app .container{display:grid;grid-template-rows:1fr repeat(2, -webkit-max-content);grid-template-rows:1fr repeat(2, max-content);width:60rem;max-height:100%;border:4px solid #2980b9;border:4px solid var(--color-blue-dark);border-radius:10px;padding-top:1rem;background:#fff;margin:2rem;box-shadow:3px 5px 5px rgba(0,0,0,0.3)}.app .container .btn{background:none;border:none;cursor:pointer;-webkit-transition:all 0.05s;transition:all 0.05s}.app .container .btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.app .container .todo-list{flex-direction:column;overflow:auto scroll}.app .container .todo-list .todo{font-size:1.8rem;display:flex;padding:1.5rem 1rem}.app .container .todo-list .todo:not(:last-of-type){border-bottom:1px solid #bdc3c7;border-bottom:1px solid var(--color-silver)}.app .container .todo-list .todo .btn-circle{margin-right:1rem}.app .container .todo-list .todo .btn-circle .circle-check{color:#2ecc71;color:var(--color-green)}.app .container .todo-list .todo .btn-circle .circle-ongoing{color:#333;color:var(--color-black)}.app .container .todo-list .todo-text{margin-right:auto}.app .container .todo-list .todo-text-complete{text-decoration:line-through;color:#555;color:var(--color-black-light)}.app .container .todo-list .todo-text-ongoing{text-decoration:none;color:#333;color:var(--color-black)}.app .container .todo-list .todo .btn-update{color:#f1c40f;color:var(--color-yellow);margin-right:2rem}.app .container .todo-list .todo .btn-update:hover{color:#ddb30c;color:var(--color-yellow-dark)}.app .container .todo-list .todo .btn-delete{color:#e74c3c;color:var(--color-red)}.app .container .todo-list .todo .btn-delete:hover{color:#c0392b;color:var(--color-red-dark)}.app .container .options{border-top:2px solid #3498db;border-top:2px solid var(--color-blue);display:flex;justify-content:space-evenly;padding:1rem 0.5rem}.app .container .options .option{font-size:1.6rem;color:#fff;flex:1 0;height:3.5rem;margin:0 3px;border-radius:5px}.app .container .options .option-active{color:#333 !important;color:var(--color-black) !important}.app .container .options .option:hover{background-color:#555;background-color:var(--color-black-light)}.app .container .options .btn-all{background-color:#3498db;background-color:var(--color-blue)}.app .container .options .btn-all:hover{background-color:#2980b9;background-color:var(--color-blue-dark)}.app .container .options .btn-complete{background-color:#2ecc71;background-color:var(--color-green)}.app .container .options .btn-complete:hover{background-color:#27ae60;background-color:var(--color-green-dark)}.app .container .options .btn-incomplete{background-color:#f39c12;background-color:var(--color-orange)}.app .container .options .btn-incomplete:hover{background-color:#e69412;background-color:var(--color-orange-dark)}.app .container .options .btn-delete-all{background-color:#e74c3c;background-color:var(--color-red)}.app .container .options .btn-delete-all:hover{background-color:#c0392b;background-color:var(--color-red-dark)}.app .container .bottom{display:flex;align-items:center;padding:1rem}.app .container .bottom .add-todo{display:flex;flex:2 1}.app .container .bottom .add-todo .input-todo{flex:1 1;width:0;min-width:0;font-size:1.4rem;font-family:inherit;color:inherit;padding:1rem;border-radius:15px;border:2px solid #bdc3c7;border:2px solid var(--color-silver);margin-right:5px;-webkit-transition:border-color 0.1s;transition:border-color 0.1s}.app .container .bottom .add-todo .input-todo:focus{outline:none;border-color:#3498db}.app .container .bottom .add-todo .btn-submit{color:#3498db;color:var(--color-blue)}.app .container .bottom .add-todo .btn-submit:hover{color:#2980b9;color:var(--color-blue-dark)}.app .container .bottom .todos-left{display:flex;flex:1 1;justify-content:center}.app .container .bottom .todos-left-text{font-size:2rem}@media only screen and (max-width: 56.25em){html{font-size:50%}.app .container{width:100%;margin:0;border-radius:0}}

